.joe__topics {
  @extend %clearfix;
  background-color: $navy;
  padding: 1px 0 $gutter-mobile;
  
  @include breakpoint($bp-med) {
    padding: 1.5em 0 2.5em;
  }
}

.joe__topics__label {
  @include type($font-serif, .8em, $font-weight-bold, 1.25);
  margin-bottom: calc($gutter / 2); 
  text-transform: uppercase;
  color: rgba($white, .8);
  text-align: center;
}

.joe__topics__list {
  list-style: none;
  margin: 2em 0 1em;
  padding: 0;
  display: flex;
  flex-wrap: wrap;

  li {
    width: 100%;
  }
  
  @include breakpoint($bp-med) {
    li {
      width: 47%;
      display: inline-block;
      float: left;
      
      &:nth-of-type(odd){
        padding-right: calc($gutter / 2);
      }
      
      &:nth-of-type(even){
        padding-left: calc($gutter / 2);
      }
    }
  }
}
