.joe__issue-header {
  text-align: center;
  margin: 1em auto 2em;
  max-width: 1000px;
  border-bottom: 1px solid $black-05;
  padding-bottom: 1.5em;
}

.joe__issue-header__date {
  @include type($font-serif, .85em, $font-weight-bold, 1.25);
  text-transform: uppercase;
  letter-spacing: .075em;
  color: $blue-darkest;
}

.joe__issue-header__title {
  margin-top: 1em;
  margin-bottom: .25em;
}

.joe__issue-header__desc {
  @extend %joe__type--small;
  text-align: left;
}

.joe__issue-header__meta {
  @extend %joe__type--smaller;
  margin-top: 1em;
}

.joe__issue-header__download {
  @include type($font-serif, .9em, $font-weight-bold, 1.35);
  display: block;
  margin-top: .25em;
}
