.vc-text-block {
  .lede {
    @include type($font-serif, 1em, $font-weight-medium, 1.5);

    @include breakpoint($bp-large) {
      @include type($font-serif, 1.15em, $font-weight-medium, 1.5);
    }
  }
}

:root[class*='vc-'] {

  // Remove the top margin from first element in content area
  .vc-text-block>*:first-child {
    margin-block-start: 0;
  }
}
