.joe__article-hero {
  position: relative;
  width: 100%;
  height: 400px;
  margin: 0 auto;
  overflow: hidden;
  background-color: $orange;
  background: $gold-gradient;
  z-index: -1;
  margin-bottom: -250px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  @include breakpoint($bp-xs) {
    height: 600px;
    margin-bottom: -400px;
  }

  @include breakpoint($bp-small) {
    height: 700px;
    margin-bottom: -425px;
  }

  @include breakpoint($bp-med) {
    height: 800px;
    margin-bottom: -500px;
  }

  @include breakpoint($bp-large) {
    height: 900px;
    margin-bottom: -525px;
  }
}

.joe__article-hero--no-image {
  height: 400px;
  margin-bottom: -375px;
  max-width: none;

  @include breakpoint($bp-med) {
    height: 800px;
    margin-bottom: -725px;
  }
  .joe__article-hero__overlay {
    display: none;
  }
}

.joe__article-hero__overlay {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 50%;
  margin: 0 auto;
  z-index: 0;
  background: $white-gradient;
}

